<template>
  <div id="vote">
    <el-form ref="myform" :inline="true" :model="formData" label-width="80px" :rules="rules">
      <el-form-item label="分数" prop="mark">
        <el-input v-model.trim="formData.mark" type="number" class="input" placeholder="请输入分数"></el-input>
      </el-form-item>
      <el-form-item class="operation_btn">
        <el-button class="btn" type="primary" size="medium" @click="login">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {postVote} from '@/api/vote'

export default {
  name: 'vote',
  props: ['activeName'],
  data () {
    let validateMark = (rule, value, callback) => {
      if (value > 10 || value < 1) {
        callback(new Error('请输入1-10之间的数字'))
      } else {
        callback()
      }
    }
    return {
      formData: {
        mark: ''
      },
      rules: {
        mark: [
          { required: true, message: '请输入分数', trigger: 'blur' },
          { validator: validateMark, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    login () {
      this.$refs['myform'].validate((valid) => {
        if (valid) {
          this.postVoteFun(this.formData)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    async postVoteFun (params) {
      let {data} = await postVote(params)
      if (data.code === 1 && data.results) {
        this.formData.mark = ''
        this.$alert(data.message, '提示')
        this.audioSay(data.message)
        this.$emit('update:activeName', '2')
      } else {
        this.$alert(data.message, '提示')
        this.audioSay(data.message)
      }
    },
    audioSay (word) {
      let audio = new Audio(
        'http://tts.baidu.com/text2audio?lan=zh&ie=UTF-8&text=' + encodeURI(word)
      )
      audio.play()
    }
  }
}
</script>

<style scoped lang="scss">
  .input {
    width: 3.2rem;
    margin-bottom: .3rem;
  }
  .operation_btn {
    margin-left: 80px;
  }
</style>
<style lang="scss">
  #vote {
    .input {
      margin-bottom: 0!important;
    }
  }
</style>



